.doCenter{
  max-width: 200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;

  .image{
    max-width: 100px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
  }

  .linkDecorate{
    text-decoration: none;
    color: black;
  }

  .spanName{
    color: silver;
  }
  .spanStatus{
    color: silver;
    word-break: break-all;
  }

}


.buttonFollow{
  max-width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  margin-bottom: 1%;
}
.buttonMessage{
  max-width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  margin-bottom: 5%;
}
